@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 300;
  src: local("Montserrat"), url("https://fonts.cdnfonts.com/s/14883/Montserrat-Light.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 900;
  src: local("Montserrat"), url("https://fonts.cdnfonts.com/s/14883/Montserrat-Black.woff") format("woff");
}
@font-face {
  font-family: "Blacker Display";
  font-style: normal;
  font-weight: 300, 500, 700;
  src: local("Blacker Display"), url("https://fonts.cdnfonts.com/s/28160/Blacker-Display-Bold-trial.woff") format("woff");
}
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  overflow-x: unset;
  padding: 0;
  font-family: "Blacker Display";
  user-select: none;
  background: #d8b188;
  background: linear-gradient(180deg, #d8b188 73%, #d89e63 0%);
  color: #313733;
}
@media (min-width: 767px) {
  body {
    background: linear-gradient(180deg, #d8b188 78%, #d89e63 0%);
  }
}

.contenedor {
  width: 98%;
  max-width: 140rem;
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0;
  font-weight: 300;
  text-align: center;
  color: #313733;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

p {
  font-size: 2rem;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

