.header {
  background-image: url("/build/img/piramides2.webp");
  height: 110vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center -10rem;
  background-attachment: fixed;
}
@media (min-width: 767px) {
  .header {
    height: 110vh;
  }
}

.top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 2rem;
  padding-left: 2rem;
  padding-right: 2rem;
}

.logotipe h1 {
  margin: 0;
  text-transform: uppercase;
  color: #FEFEFE;
  font-weight: 700;
}
.logotipe h1 span {
  color: #AD764A;
}

.navegation {
  display: none;
}
@media (min-width: 1023px) {
  .navegation {
    display: block;
  }
  .navegation ul {
    display: flex;
    gap: 4rem;
    margin-bottom: 0;
    width: 100rem;
    justify-content: flex-end;
  }
  .navegation ul a {
    color: #FEFEFE;
    font-size: 2rem;
    text-transform: capitalize;
  }
}

.box {
  width: 3rem;
  height: 3rem;
}

.center {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10rem;
}

.social {
  padding-top: 8rem;
}
.social li {
  transform: rotate(270deg);
}
.social a {
  color: #FEFEFE;
}

.centerBox {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.centerBox span {
  text-transform: uppercase;
  color: #bf906b;
  font-weight: 700;
  position: relative;
  padding-bottom: 2rem;
}
.centerBox span::before {
  content: "";
  position: absolute;
  width: 5rem;
  height: 0.3rem;
  background-color: currentColor;
  bottom: 0;
  left: 3rem;
}
.centerBox .subtitle {
  max-width: 90rem;
  color: #FEFEFE;
  font-weight: 700;
  line-height: normal;
  padding-top: 8rem;
  font-size: 3rem;
}
@media (min-width: 767px) {
  .centerBox .subtitle {
    font-size: 6rem;
  }
}
.centerBox .scrolldown {
  color: #6fa1b3;
  text-align: center;
  line-height: 3rem;
  margin-top: 5rem;
  text-transform: uppercase;
  font-weight: 700;
}
.centerBox .scrolldown i {
  color: #86b0bf;
  font-size: 5rem;
}

.gotoegypt {
  writing-mode: tb;
  padding-top: 8rem;
}
.gotoegypt p {
  margin: 0 auto;
  color: #FEFEFE;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
}

.hola {
  height: 100vh;
}

.civilization {
  text-align: center;
  line-height: normal;
  margin-top: 8rem;
}

.civiInfo {
  text-align: start;
  line-height: 2;
}
@media (min-width: 767px) {
  .civiInfo {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
}
.civiInfo p {
  padding-right: 1rem;
  padding-left: 1rem;
  color: #FEFEFE;
}
@media (min-width: 1023px) {
  .civiInfo p {
    padding-right: 10rem;
    padding-left: 10rem;
  }
}

iframe {
  width: 90%;
  height: 20rem;
}
@media (min-width: 767px) {
  iframe {
    width: 95%;
    height: 60rem;
  }
}
@media (min-width: 1023px) {
  iframe {
    width: 75%;
  }
}

.titleSection {
  font-family: "Montserrat";
  font-weight: 900;
  font-size: 1.6rem;
  text-transform: uppercase;
  color: #6d7a72;
  margin: 0;
  margin-bottom: 1.6rem;
}

.subtitleSection {
  font-size: 3.4rem;
  margin: 0;
  text-transform: capitalize;
  color: #FEFEFE;
}
.subtitleSection span {
  color: #616d65;
}

.things {
  text-align: center;
  line-height: normal;
  margin-top: 8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 15rem;
}

.thingsSubtitle {
  font-size: 3.4rem;
  margin: 0;
  text-transform: capitalize;
  color: #FEFEFE;
  width: 35rem;
  margin-bottom: 5rem;
}
.thingsSubtitle span {
  color: #616d65;
}
@media (min-width: 767px) {
  .thingsSubtitle {
    margin-bottom: 10rem;
    width: 50rem;
  }
}

.thingsContainer {
  margin-top: 8rem;
  width: 95%;
}

.thing {
  margin-bottom: 43rem;
  width: 98%;
}
@media (min-width: 767px) {
  .thing {
    margin-bottom: 55rem;
  }
}
@media (min-width: 1023px) {
  .thing {
    display: grid;
    gap: 10rem;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 20rem;
    width: auto;
  }
}
.thing .thingDescription {
  text-align: start;
  width: 98%;
  font-family: "Montserrat";
}
@media (min-width: 767px) {
  .thing .thingDescription {
    width: auto;
  }
}
.thing .thingSingle {
  color: #FEFEFE;
  text-align: start;
  font-size: 3rem;
  font-weight: 700;
  width: 98%;
  margin-bottom: 3rem;
}
@media (min-width: 767px) {
  .thing .thingSingle {
    width: 40rem;
    margin-bottom: 2rem;
  }
}
.thing .thingDesc {
  color: #FEFEFE;
  font-size: 2.2rem;
  line-height: 1.4;
  width: 95%;
  text-align: justify;
}
@media (min-width: 767px) {
  .thing .thingDesc {
    width: auto;
  }
}
.thing .readMore {
  color: #616d65;
  text-transform: capitalize;
  font-size: 2rem;
  text-decoration: underline;
}

.thing-2 .thingDescription {
  grid-column: 2/3;
}
.thing-2 .thingImage {
  grid-column: 1/2;
  grid-row: 1/2;
}

.thingImage {
  position: relative;
  margin-top: 10rem;
  width: 95%;
}
@media (min-width: 1023px) {
  .thingImage {
    margin-top: 0;
    width: auto;
  }
}

.shadow {
  position: absolute;
  width: 25rem;
  height: 30rem;
  background: #e1c1a3;
  background: linear-gradient(180deg, rgba(225, 193, 163, 0.5690476874) 70%, rgba(216, 158, 99, 0) 100%);
  transform: rotate(-20deg);
  border-radius: 2rem;
  top: -6rem;
  right: 3rem;
}
@media (min-width: 767px) {
  .shadow {
    width: 35rem;
    height: 40rem;
    right: 13rem;
  }
}

.Tutankhamun {
  position: absolute;
  top: -2rem;
  right: 6rem;
  width: 20rem;
  height: 20rem;
}
@media (min-width: 767px) {
  .Tutankhamun {
    width: 30rem;
    height: 35rem;
    top: -4rem;
    right: 15rem;
  }
}

.showMore {
  padding: 2rem 5rem;
  border: 1px solid #313733;
  text-align: center;
  text-transform: uppercase;
  color: #313733;
  align-content: center;
  font-size: 1.8rem;
}

.experiences {
  text-align: center;
  margin-bottom: 15rem;
  height: 70vh;
  width: 90%;
}
@media (min-width: 767px) {
  .experiences {
    max-width: none;
  }
}

.descExperiences {
  color: #FEFEFE;
  max-width: 60rem;
  margin: 7rem auto;
}

.icon-cards {
  position: relative;
  width: 60vw;
  height: 30vw;
  max-width: 38rem;
  margin: 0 auto;
  perspective: 90rem;
  margin-top: 3rem;
}
.icon-cards__content {
  position: absolute;
  width: 100%;
  height: 100%;
  transform-origin: center;
  transform-style: preserve-3d;
  transform: translateZ(-30vw) rotateY(0);
  animation: carousel 10s infinite cubic-bezier(0.77, 0, 0.175, 1) forwards;
  margin-top: 8rem;
}
.icon-cards__content.step-animation {
  animation: carousel 8s infinite steps(1) forwards;
}
.icon-cards__content img {
  width: 42rem;
  min-width: 100%;
  height: 35rem;
}
@media (min-width: 767px) {
  .icon-cards__content img {
    height: 50rem;
  }
}
.icon-cards .info-card {
  position: absolute;
  bottom: -8rem;
  left: 0;
  right: 0;
  background-color: rgba(49, 55, 51, 0.5);
  width: 100%;
  height: auto;
}
.icon-cards .info-card .titleSection {
  color: #313733;
  margin-bottom: 0.4rem;
}
.icon-cards__item {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 60vw;
  height: 40vw;
  max-width: 38rem;
  max-height: 25rem;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  border-radius: 0.6rem;
  transform-origin: center;
}
.icon-cards__item:nth-child(1) {
  transform: rotateY(0) translateZ(27vw);
}
.icon-cards__item:nth-child(2) {
  transform: rotateY(-120deg) translateZ(27vw);
}
.icon-cards__item:nth-child(3) {
  transform: rotateY(-240deg) translateZ(27vw);
}

.mt-3 {
  margin-top: 10rem !important;
}

.thanks {
  text-align: center;
  max-width: 98%;
  padding-top: 10rem;
}
@media (min-width: 767px) {
  .thanks {
    padding-top: 0rem;
  }
}

.partners {
  display: flex;
  flex-direction: column;
  gap: 10rem;
  margin-top: 10rem;
  margin-bottom: 10rem;
}
@media (min-width: 767px) {
  .partners {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 10rem;
    margin-bottom: 15rem;
  }
}
@media (min-width: 1023px) {
  .partners {
    display: flex;
    justify-content: center;
    flex-direction: row;
    gap: 15rem;
    margin-top: 10rem;
    margin-bottom: 15rem;
  }
}

.containerPartners {
  display: flex;
  flex-direction: column;
}

.imageSponsor {
  width: 14rem;
  height: 5rem;
}

.contenedorjinete {
  display: flex;
  justify-content: center;
  padding-top: 5rem;
  width: 100%;
}

.jinete {
  width: 25rem;
  height: 30rem;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-top: 4rem;
  padding-bottom: 3rem;
  margin-top: 5rem;
  border-top: 1px solid #AD764A;
}
.footer p {
  text-transform: capitalize;
  color: #385D6A;
  font-size: 1.5rem;
}

@keyframes carousel {
  0%, 17.5% {
    transform: translateZ(-35vw) rotateY(0);
  }
  27.5%, 45% {
    transform: translateZ(-35vw) rotateY(-120deg);
  }
  55%, 72.5% {
    transform: translateZ(-35vw) rotateY(-240deg);
  }
  82.5%, 100% {
    transform: translateZ(-35vw) rotateY(-360deg);
  }
}